.tui-pagination .tui-is-selected, .tui-pagination strong {
    color: #fff;
    background: #FF6B08;
    border-color: #FF6B08;
    border-radius: 5px;
    cursor: default;
}

.tui-page-btn:first-child,
.tui-page-btn:last-child {
   display: none;
}
.tui-pagination .tui-first, .tui-pagination .tui-prev, .tui-pagination .tui-next, .tui-pagination .tui-last, .tui-pagination .tui-prev-is-ellip, .tui-pagination .tui-next-is-ellip {
    overflow: hidden;
    width: 40px;
    height: 40px;
    padding: 8px;
    font-size: 0;
    line-height: 26px;
    white-space: nowrap;
    font-weight: normal;
  
}
.tui-pagination .tui-prev {
    transform: matrix(-1, 0, 0, 1, 0, 0);
    background: #F7F7F7;
    vertical-align: middle;
    margin-right: 10px;
    border-radius: 5px;
    background-image: url(../images/arrow-left.png);
    background-position: center;
    background-repeat: no-repeat;
    //  background-color:rgba(247, 247, 247, 0.0);
}

.tui-pagination .tui-next {
    margin-left: 10px;
    // background: #F7F7F7;
    border-radius: 5px;
    transform: matrix(1, 0, 0, 1, 0, 0);
    background-image: url(../images/arrow-left.png);
    background-position: center;
    background-repeat: no-repeat;
    background-color: #F7F7F7;
//  background-color:rgba(247, 247, 247, 0.0);
}

.tui-pagination .tui-prev:hover {
    background-color: #F7F7F7;
    background-image: url(../images/arrow-left.png);
    background-position: center;
    background-repeat: no-repeat;
        // background-color:rgba(247, 247, 247, 0.0);
    color: rgb(27, 25, 25);
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
.tui-pagination .tui-next:hover {
    background-color: #F7F7F7;
    background-image: url(../images/arrow-left.png);
    background-position: center;
    background-repeat: no-repeat;
    color: #fff;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

.tui-pagination .tui-ico-first {
    width: 11px;
    height: 10px;
    background-position: 0px 0px
}

.tui-pagination .tui-ico-prev {
    width: 0px;
    height: 10px;
    background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
    width: 0px;
    height: 10px;
    background-position: -13px -24px;
}

 .tui-ico-last {
    width: 11px;
    height: 10px;
    background-position: 0px -12px
}

 .tui-ico-ellip {
    width: 10px;
    height: 2px;
    margin-top: 16px;
    vertical-align: top;
    background-position: 0px -24px
}
 a.tui-pagination .tui-page-btn {
    align-items: center;
 }

